'use client';

import * as React from 'react';
import { useActionState } from 'react';
import { Button } from '@/components/ui/button';
import { getHypotheticalScenario, getRandomQuestion, type QuestionState, type ScenarioState } from '@/app/actions';
import { useToast } from '@/hooks/use-toast';
import { Bot, Dice5, Send, X } from 'lucide-react';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { ScrollArea } from '@/components/ui/scroll-area';
import { useLanguage } from '@/context/language-context';
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from '@/components/ui/tooltip';

interface AiExplainerProps {
  objectName: string;
}

interface Message {
  id: number;
  text: string;
  sender: 'user' | 'ai';
}

const initialScenarioState: ScenarioState = { success: false };
const initialQuestionState: QuestionState = { success: false };

export function AiExplainer({ objectName }: AiExplainerProps) {
  const { t, language } = useLanguage();
  const { toast } = useToast();

  const [scenarioState, submitScenario, isScenarioPending] = useActionState(getHypotheticalScenario, initialScenarioState);
  const [randomQuestionState, submitRandomQuestion, isQuestionPending] = useActionState(getRandomQuestion, initialQuestionState);

  const [isOpen, setIsOpen] = React.useState(false);
  const [messages, setMessages] = React.useState<Message[]>([]);
  const scenarioInputRef = React.useRef<HTMLTextAreaElement>(null);

  React.useEffect(() => {
    if (scenarioState.success && scenarioState.evaluation) {
        setMessages(prev => [...prev, { id: Date.now(), text: scenarioState.evaluation!, sender: 'ai' }]);
    } else if (scenarioState.error) {
      toast({
        variant: 'destructive',
        title: t('toast.error.title'),
        description: scenarioState.error || t('toast.error.scenarioFailed'),
      });
    }
  }, [scenarioState, toast, t]);

  React.useEffect(() => {
    if (randomQuestionState.success && randomQuestionState.question) {
        if(scenarioInputRef.current) {
            scenarioInputRef.current.value = randomQuestionState.question;
            scenarioInputRef.current.focus();
        }
    } else if (randomQuestionState.error) {
      toast({
        variant: 'destructive',
        title: t('toast.error.title'),
        description: randomQuestionState.error || t('toast.error.randomQuestionFailed'),
      });
    }
  }, [randomQuestionState, toast, t]);

  const handleFormSubmit = (formData: FormData) => {
    const scenario = formData.get('scenario') as string;
    if (!scenario?.trim()) {
        toast({
            variant: 'destructive',
            title: t('toast.error.title'),
            description: t('toast.error.scenarioRequired'),
        });
        return;
    }
    setMessages(prev => [...prev, { id: Date.now(), text: scenario, sender: 'user' }]);
    submitScenario(formData);
    if(scenarioInputRef.current) {
        scenarioInputRef.current.value = '';
    }
  }

  const handleRandomQuestionClick = () => {
    const formData = new FormData();
    formData.append('objectName', objectName);
    formData.append('language', language === 'es' ? 'Spanish' : 'English');
    submitRandomQuestion(formData);
  };

  const isPending = isScenarioPending || isQuestionPending;

  return (
    <div>
        <Button 
            onClick={() => setIsOpen(!isOpen)}
            className="fixed bottom-4 right-4 z-50 rounded-full w-16 h-16 shadow-lg"
            aria-label={t('ai.hypothetical.title')}
        >
            {isOpen ? <X className="h-8 w-8" /> : <Bot className="h-8 w-8" />}
        </Button>

        {isOpen && (
            <Card className="fixed bottom-24 right-4 z-50 w-full max-w-sm flex flex-col shadow-xl" style={{ height: '60vh'}}>
                <CardHeader>
                    <CardTitle>{t('ai.hypothetical.title')}</CardTitle>
                </CardHeader>
                <CardContent className="flex-grow overflow-hidden">
                    <ScrollArea className="h-full pr-4">
                        <div className="space-y-4">
                            {messages.map((message) => (
                                <div key={message.id} className={`flex items-end gap-2 ${message.sender === 'user' ? 'justify-end' : ''}`}>
                                    {message.sender === 'ai' && <Bot className="w-6 h-6 flex-shrink-0" />}
                                    <div className={`rounded-lg px-3 py-2 text-sm ${message.sender === 'user' ? 'bg-primary text-primary-foreground' : 'bg-muted'}`}>
                                        {message.text}
                                    </div>
                                </div>
                            ))}
                             {isScenarioPending && (
                                <div className="flex items-end gap-2">
                                    <Bot className="w-6 h-6 flex-shrink-0" />
                                    <div className="rounded-lg px-3 py-2 text-sm bg-muted animate-pulse">
                                        {t('ai.hypothetical.loading')}
                                    </div>
                                </div>
                            )}
                        </div>
                    </ScrollArea>
                </CardContent>
                <CardFooter>
                     <form action={handleFormSubmit} className="flex w-full items-center space-x-2">
                        <Textarea
                            id="hypothetical-scenario"
                            name="scenario"
                            ref={scenarioInputRef}
                            placeholder={t('ai.hypothetical.placeholder')}
                            className="flex-1 resize-none"
                            disabled={isPending}
                        />
                        <input type="hidden" name="objectName" value={objectName} />
                        <input type="hidden" name="language" value={language === 'es' ? 'Spanish' : 'English'} />
                        
                        <TooltipProvider>
                            <Tooltip>
                                <TooltipTrigger asChild>
                                    <Button variant="outline" size="icon" type="button" onClick={handleRandomQuestionClick} disabled={isPending}>
                                        <Dice5 className="h-4 w-4" />
                                        <span className="sr-only">{t('ai.hypothetical.randomQuestion')}</span>
                                    </Button>
                                </TooltipTrigger>
                                <TooltipContent>
                                    <p>{t('ai.hypothetical.randomQuestion')}</p>
                                </TooltipContent>
                            </Tooltip>
                        </TooltipProvider>
                        
                        <Button type="submit" size="icon" disabled={isPending}>
                            <Send className="h-4 w-4" />
                            <span className="sr-only">{t('quiz.submit')}</span>
                        </Button>
                    </form>
                </CardFooter>
            </Card>
        )}
    </div>
  );
}
